body {
    background-color: #00FF87;
    padding: 20px;
    margin: 0;
}
.overHeader {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.headerText {
    width: 30%;
    min-width: 270px;
}

.headerArt {
    width: 70%;
    margin: 0;
    background: url('https://ismdj.scdn5.secure.raxcdn.com/static/libsass/plfpl/dist/img/header-bg-large.png?109fa73b2dbf') no-repeat right -50px bottom;
    background-position: right -250px top;
    height: 130px;
    vertical-align: middle;
}

h1, h2, p, ul, li {
    font-family: Helvetica, Arial, sans-serif;
}

ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
}

ul.header {
    background-color: #111;
    padding: 0;
}

ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
}

.content {
    background-color: ghostwhite;
    padding: 20px;
}

.content h2 {
    padding: 0;
    margin: 0;
}

.content li {
    margin-bottom: 10px;
}

.active {
    background-color: #0099FF;
}



